import React, { useEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectCoverflow, Pagination, Navigation } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import './Services.css';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

export const Services = () => {
  const services = [
    {
      title: "COACHING DI ELITE",
      description: "Programmi personalizzati per superare ogni limite e scolpire il tuo fisico ideale."
    },
    {
      title: "PERSONAL TRAINING INTENSIVO",
      description: "Sessioni one-on-one che ti spingeranno oltre i tuoi limiti, in presenza e online."
    },
    {
      title: "NUTRIZIONE DA CAMPIONI",
      description: "Piani alimentari su misura per alimentare la tua trasformazione e massimizzare i risultati."
    },
    {
      title: "PROGRAMMI DI DOMINIO TOTALE",
      description: "Schede di allenamento create per conquistare ogni tuo obiettivo, senza compromessi."
    }
  ];

  useEffect(() => {
    gsap.to('.services-title', {
      opacity: 1,
      y: 0,
      duration: 1,
      scrollTrigger: {
        trigger: '.services',
        start: 'top bottom-=100',
        end: 'bottom top',
        toggleActions: 'play none none reverse',
      }
    });
  }, []);

  return (
    <section id="services" className="services">
      <div className="services-header">
        <h2 className="services-title">DOMINA CON I MIEI SERVIZI</h2>
      </div>
      <Swiper
        effect={'coverflow'}
        grabCursor={true}
        centeredSlides={true}
        slidesPerView={'auto'}
        coverflowEffect={{
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true,
        }}
        pagination={true}
        navigation={true}
        modules={[EffectCoverflow, Pagination, Navigation]}
        className="mySwiper"
        touchReleaseOnEdges={true}
        aria-label="Carosello dei servizi" 
      >
        {services.map((service, index) => (
          <SwiperSlide key={index}>
            <div className="service-card">
              <h3>{service.title}</h3>
              <p>{service.description}</p>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </section>
  );
};